@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
.App {
  text-align: center;
  font-family: "Poppins", sans-serif;
}

.todo-card{display:flex;flex-direction:column;border:1px solid;border-radius:10px;background-color:#cddcdc;background-image:radial-gradient(at 50% 100%, rgba(255,255,255,0.5) 0%, rgba(0,0,0,0.5) 100%),linear-gradient(to bottom, rgba(255,255,255,0.25) 0%, rgba(0,0,0,0.25) 100%);background-blend-mode:screen, overlay;padding:20px}.todo-card__status--unfinished{color:red}.todo-card__status--done{color:green}

.container{margin:0 auto;width:75vw;display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:10px;border-radius:10px}

